




























































































































































@import '~@/styles/variables';

/deep/ #map {
  .states {
      stroke: $dark;
      stroke-width: 1px;
      fill: $white;
      stroke-linejoin: round;
  }
  .states:hover {
      fill: $sky-blue;
      opacity: 1.0;
  }
  .state-borders {
      fill: none;
      stroke: $dark;
      stroke-width: 3px;
      stroke-linejoin: round;
      stroke-linecap: round;
      pointer-events: none;
  }
  .marker {
      opacity: 1;
      stroke-width: 5;
      stroke: black;

      &:hover {
        /* fill: #c65067; */
        opacity: 1;
      }
  }
  .marker-category-0 {
    fill: $red;
  }
  .marker-category-1 {
    fill: $dusk;
  }
  .marker-category-3 {
    fill: $dark;
  }
}

